
import { defineComponent, PropType } from 'vue';
import { useI18n } from 'vue-i18n';
import { useRouter } from 'vue-router';
import List from '@/components/list/List.vue';
import ListItem from '@/components/list/ListItem.vue';
import ListContent from '@/components/list/ListContent.vue';
import IconButton from '@/components/IconButton.vue';
import ToolTip from '@/components/ToolTip.vue';
import { Habit } from '@/types/shared';
import { getLocalDate } from '@/services/utils/time';
import { habitDetail } from '@/services/utils/link';
import { getCategory } from '@/types/categories';
import Tag from '@/components/Tag.vue';

export default defineComponent({
  components: {
    List,
    ListItem,
    ListContent,
    IconButton,
    ToolTip,
    Tag,
  },
  props: {
    habits: {
      type: Object as PropType<Habit[]>,
      required: true,
    },
    isArchive: {
      type: Boolean,
      default: false,
    },
  },
  setup() {
    const { t } = useI18n();
    const router = useRouter();
    const getDays = (habit: Habit) => habit.days.map((day) => t(`days.full.${day}`)).join(', ');
    const getDateString = (date: number) => getLocalDate(date || 0);

    const goToAnalytics = (id: string | number) => {
      router.push({
        name: habitDetail.name,
        params: {
          id,
        },
      });
    };

    return {
      t,
      getDays,
      getDateString,
      goToAnalytics,
      getCategory,
    };
  },
});
