<template>
  <list class="habits-list">
    <list-item v-for="habit in habits" :key="habit.id">
      <list-content>
        <template #middle>
          <img
            v-if="habit.isPrioritized"
            src="@/assets/images/icons/star.svg"
            alt="open"
            class="daily-habit-item__prioritized"
          /> {{habit.description}}
          <p v-if="habit.startTime && habit.endTime">
            <time :datetime="habit.startTime" class="time-block">{{habit.startTime}}</time> -
            <time :datetime="habit.startTime" class="time-block">{{habit.endTime}}</time>
          </p>
          <p class="text-sm text-gray-400">
            {{ getDays(habit) }}
          </p>
          <p v-if="habit.endDate" class="text-sm text-gray-400">
            {{t('habitsPage.ends')}} {{getDateString(habit.endDate)}}
          </p>
          <p v-if="habit.categoryId">
            <tag variant="indigo">{{t(`categories.${getCategory(habit.categoryId)?.name}`)}}</tag>
          </p>
        </template>
        <template #right>
          <tool-tip v-if="!isArchive">
            <template #element>
              <icon-button :flat="true"
                @click="goToAnalytics(habit.id)"
              >
                <img src="@/assets/images/icons/analytics.svg" alt="analytics" />
              </icon-button>
            </template>
            <template #text>{{t('habitsPage.itemBtn.analytics')}}</template>
          </tool-tip>
          <tool-tip v-if="!isArchive">
            <template #element>
              <icon-button :flat="true" @click="$emit('edit', habit)">
                <img src="@/assets/images/icons/edit.svg" alt="edit" />
              </icon-button>
            </template>
            <template #text>{{t('habitsPage.itemBtn.edit')}}</template>
          </tool-tip>
          <tool-tip v-if="isArchive" :right="true">
            <template #element>
              <icon-button :flat="true"
                @click="$emit('info',habit.id)"
              >
                <img src="@/assets/images/icons/info.svg" alt="info" />
              </icon-button>
            </template>
            <template #text>{{t('habitsPage.itemBtn.info')}}</template>
          </tool-tip>
          <tool-tip :right="true">
            <template #element>
              <icon-button :flat="true"
                @click="$emit('delete',habit)"
              >
                <img src="@/assets/images/icons/delete.svg" alt="delete" />
              </icon-button>
            </template>
            <template #text>{{t('habitsPage.itemBtn.delete')}}</template>
          </tool-tip>
        </template>
      </list-content>
    </list-item>
  </list>
</template>

<script lang="ts">
import { defineComponent, PropType } from 'vue';
import { useI18n } from 'vue-i18n';
import { useRouter } from 'vue-router';
import List from '@/components/list/List.vue';
import ListItem from '@/components/list/ListItem.vue';
import ListContent from '@/components/list/ListContent.vue';
import IconButton from '@/components/IconButton.vue';
import ToolTip from '@/components/ToolTip.vue';
import { Habit } from '@/types/shared';
import { getLocalDate } from '@/services/utils/time';
import { habitDetail } from '@/services/utils/link';
import { getCategory } from '@/types/categories';
import Tag from '@/components/Tag.vue';

export default defineComponent({
  components: {
    List,
    ListItem,
    ListContent,
    IconButton,
    ToolTip,
    Tag,
  },
  props: {
    habits: {
      type: Object as PropType<Habit[]>,
      required: true,
    },
    isArchive: {
      type: Boolean,
      default: false,
    },
  },
  setup() {
    const { t } = useI18n();
    const router = useRouter();
    const getDays = (habit: Habit) => habit.days.map((day) => t(`days.full.${day}`)).join(', ');
    const getDateString = (date: number) => getLocalDate(date || 0);

    const goToAnalytics = (id: string | number) => {
      router.push({
        name: habitDetail.name,
        params: {
          id,
        },
      });
    };

    return {
      t,
      getDays,
      getDateString,
      goToAnalytics,
      getCategory,
    };
  },
});
</script>
